import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

// 价格计算
const setTotalPrices = (cartList)=>{
    let totalPrices = 0.00;
    if(cartList.length > 0){
        cartList.forEach(item=>{
            let price = parseFloat(item.price);
            totalPrices += price * item.count;
        })
    }
    return totalPrices.toFixed(2);
}

export default new Vuex.Store({
    state: {
        active:'',
        cartStatus:false,
        cartList:[],
        totalPrices:0.00
    },
    mutations: {
        changePage(state,str){
            state.active = str;
        },
        // 购物车切换
        changeCartStatus(state,bool){
            state.cartStatus = bool;
        },
        // 获取购物车列表
        getCartList(state){
            let cartList = localStorage.getItem('cartList');
            if(cartList){
                cartList = JSON.parse(cartList);
            }else{
                cartList = [];
            }
            state.cartList = cartList;
            state.totalPrices = setTotalPrices(cartList);
        },
        // 加入购物车
        saveCartList(state,data){
            let {cartList} = state;
            if(cartList.length != 0){
                let isIncart = cartList.findIndex(item => item.id == data.id);
                if(isIncart > -1){
                    cartList[isIncart].count += data.count;
                }else{
                    cartList = [...cartList,data];
                }
                
            }else{
                cartList = [];
                cartList.push(data);
            }
            state.cartList = cartList;
            state.totalPrices = setTotalPrices(cartList);
            localStorage.setItem('cartList',JSON.stringify(cartList));
        },
        // 删除
        deleteCartShop(state,id){
            let cartList = state.cartList.filter(item => item.id != id);
            state.cartList = cartList;
            state.totalPrices = setTotalPrices(cartList);
            localStorage.setItem('cartList',JSON.stringify(cartList))
        }
    }
});


