import Vue from 'vue'
import Router from 'vue-router'
import cookies from 'js-cookie';
import {post} from '@/http.js'

Vue.use(Router);

function isMobile() {
    var sUserAgent = navigator.userAgent;
    if (sUserAgent.indexOf('Android') > -1 || sUserAgent.indexOf('iPhone') > -1 || sUserAgent.indexOf('iPad') > -1 || sUserAgent.indexOf('iPod') > -1 || sUserAgent.indexOf('Symbian') > -1) {
        return true;
    } else { 
        return false;
    }
}

const router = new Router({
	mode: 'history',
	routes: [
        {
            //登陆
            path: "/login",
            name: 'Login',
            component: resolve => void (require(['@/views/pc/Login/Login.vue'], resolve))
        },
        {
            path: "/",
            component: resolve => void (require(['@/views/pc/Layout/Layout.vue'], resolve)),
            children:[
                {
                    path: "/",
                    name: 'Index',
                    meta:{
                        title:'Home',
                        pagename:'Home',
                    },
                    component: resolve => void (require(['@/views/pc/Index/Index.vue'], resolve))
                },
                {
                    path: "/shop",
                    name: 'Shop',
                    meta:{
                        title:'Shop',
                        pagename:'Shop',
                    },
                    component: resolve => void (require(['@/views/pc/Shop/Shop.vue'], resolve))
                },
                {
                    path: "/shop/detail",
                    name: 'Shopdetail',
                    meta:{
                        title:'Shop',
                        pagename:'Shop',
                    },
                    component: resolve => void (require(['@/views/pc/Shop/Detail.vue'], resolve))
                },
                {
                    path: "/cart",
                    name: 'Cart',
                    meta:{
                        title:'Cart',
                        pagename:'Cart',
                    },
                    component: resolve => void (require(['@/views/pc/Cart/Cart.vue'], resolve))
                },
            ]
        },
        {
            path: "/m",
            component: resolve => void (require(['@/views/wap/Layout/Layout.vue'], resolve)),
            children:[
                {
                    path: "/m",
                    name: 'wapIndex',
                    meta:{
                        title:'Home',
                        pagename:'Home',
                    },
                    component: resolve => void (require(['@/views/wap/Index/Index.vue'], resolve))
                },
                {
                    path: "/m/shop",
                    name: 'wapShop',
                    meta:{
                        title:'Shop',
                        pagename:'Shop',
                    },
                    component: resolve => void (require(['@/views/wap/Shop/Shop.vue'], resolve))
                },
                {
                    path: "/m/shop/detail",
                    name: 'wapShopdetail',
                    meta:{
                        title:'Shop',
                        pagename:'Shop',
                    },
                    component: resolve => void (require(['@/views/wap/Shop/Detail.vue'], resolve))
                },
                {
                    path: "/m/cart",
                    name: 'wapCart',
                    meta:{
                        title:'Cart',
                        pagename:'Cart',
                    },
                    component: resolve => void (require(['@/views/wap/Cart/Cart.vue'], resolve))
                },
            ]
        },
    ]
})


//路由拦截器
router.beforeEach((to, from, next) => {
	/* 路由发生变化修改页面title */
	if (to.meta.title) {
		document.title = to.meta.title
    }
    if(isMobile() && localStorage.getItem('site_type') != 'wap'){
        localStorage.setItem('site_type','wap');
        console.log('wap...')
        next({
            path: '/m'
        })
    }else if(isMobile() == false && localStorage.getItem('site_type') != 'pc'){
        localStorage.setItem('site_type','pc');
        next({
            path: '/'
        });
    }else {
        next();
    }
    // if (to.path.indexOf('/login') == -1) {// 已登录
    //     if (cookies.get('token') == undefined) {
    //         // console.log(cookies.get('token'));
           
    //         next({
    //             path: '/login'
    //         })
    //     }else{
	// 		//访问日志
	// 		// console.log(to);
	// 		let pageUrl= to.path;
	// 		let pageName= to.meta.title;
	// 		post('visitUrl/insertVisitUrl',{pageUrl,pageName})
	// 		next();
	// 	} 
    // } else {
    //     next();
    // }
});


export default router 


